import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex);

export default new vuex.Store({
    state: {
        djq:1,
        scdp:12,
        ye:123,
        jf:1230,
        location:'',
        doornumber:'',
        contectname:'',
        phonenumber:'',
    },
    mutations:{
        increment(state){
            state.djq++
        },
        setlocation(state,val){
             state.location = val
        },
        setdoornumber(state,val){
            state.doornumber = val
        },
        setcontectname(state,val){
            state.contectname = val
        },
        setphonenumber(state,val){
            state.phonenumber = val
        }
    }
})


