<template>
  <div class="ticket">
    <div class="tickettop">
        <i class="el-icon-arrow-left"  @click.stop="back"></i>
        我的订单
    </div>
    <div class="ordermiss" v-if="ordermiss">
      <img src="../../assets/order_no_con.png">
      <p>您还没有订单，赶紧点一份！</p>
      <div class="btn" @click="gofood"><a>现在点一份</a></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ticket',
  components:{
  },
  data () {
    return {
      ordermiss:true,
    }
  },
  methods:{
    gofood(){
      this.$router.push('/shop/goods')
    },
    back() {
      //this.showFlag = false;
    },
  }
}
</script>

<style lang="less">
.ticket{
  font-size:16px;
  .tickettop{
    text-align: center;
    height:40px;
    line-height: 40px;
    color:#fff;
    background: #ff2d4b;
    position: relative;
    i{
        position: absolute;
        left: 30px;
        padding: 12px;
    }
  }
  .ordermiss{
    text-align: center;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%,-50%);
    img{
      width: 100px;
      height: 100px;
    }
    p{
      margin-top: 30px;
      font-size: 16px;
      color:#768491;
      margin-bottom: 30px;
    }
    .btn{
        display: inline-block;
        height:40px;
        line-height: 40px;
        color: #fff;
        background: #ff2d4b;
        padding: 0 35px;
        border-radius: 30px;
        font-size:14px;
        a{
          color:#fff;
        }
    }
  }
}
</style>



